body {
  height: 100%;
  border: solid 2px burlywood;
}

.container-body {
  display: flex;
}

.header {
  display: flex;
  background-color:#33cccc;
  height: 70px;
  color: white;
  font-size: 32px;
  padding: 10px;
  font-family: 'Lobster', cursive;
}
.content {
  display: flex;
 
}
.ing {
  padding-right: 50px;
  line-height: 25px;
}

.instr {
  line-height: 25px;
}

img {
  height: 75px;
  width: 75px;
  border-radius: 100%;
}

.card {
  border: solid 2px burlywood;
  float: left;
  height: 40px;
  width: 70%;
  padding: 15px;
  margin: 20px;
  font-size: 22px;
  font-family: 'Hind Siliguri', sans-serif;
  color: burlywood;
  font-weight: bold;
}

input {
  width: 40%;
  height: 20px;
  margin: 20px; 
  font-size: 18px;
  border-radius: 7px;
  border: solid 2px burlywood;
  padding-left: 10px;
  font-family: 'Hind Siliguri', sans-serif;
  color: burlywood;
}

button {
  width: 10%;
  height: 20px;
  margin: 10px;
  background-color:#33cccc;
  color: white;
  font-size: 12px;
  border-radius: 7px;
  font-family: 'Hind Siliguri', sans-serif;
}

.selected {
  margin-top: 70px;
  padding: 20px;
  font-family: 'Hind Siliguri', sans-serif;
  margin-right: 10px;
  
}

.headingName {
  font-family: 'Hind Siliguri', sans-serif;
  font-size: 30px;
  color: burlywood;
  font-weight: bold;
}

.headingIngredients {
  font-family: 'Hind Siliguri', sans-serif;
  font-size: 16px;
  color: gray;
}

.headingInstructions {
  font-family: 'Hind Siliguri', sans-serif;
  font-size: 16px;
  color: gray;
}

.recipe-name {
  border: solid .5px burlywood;
  width: 50%;
}

.selected-recipe {
  border: solid .5px burlywood;
  width: 50%;
}
